.infos-wrapper{

        display: block;
        position: absolute;
        top: 50%;
        right: 30px;
        transform: translate(0,-50%);
        width: 400px;
        height: 90vh;
        /* background-color: #1; */
        border: 3px solid #c9b27f;
        color: rgb(255, 126, 244);
        color: #c9b27f;
        /* color: #ff74e8; */
        padding: 20px;
        border-radius: 2rem;
        font-family: "Digital-7";
        /* font-family: "ＭＳ Ｐゴシック"; */
        /* font-size: 1.05em; */
        font-weight: 600;
        overflow-y:scroll;
        overflow-x: hidden;
        z-index: 2 ;
        /* backdrop-filter: blur(1px); */
}

.infos-block{
    width: 400px;
    display: block;
    text-align: left;
}

@media screen and (max-width: 500px) {
    
    .infos-wrapper{
        display: none;
    }
  }